<template>
  <div>
    <LoginTop topMiddle="登录bilibili" topRight="注册" toPage="/register">
      <span slot="right" @click="$router.push('/register')">注册</span>
    </LoginTop>
    <LoginText
      label="账号"
      class="username login-field"
      placeholder="请输入账号"
      rule="^.{6,16}$"
      type="number"
      inputType="username"
      @onContentUpdate="(res) => (model.username = res)"
    />
    <LoginText
      label="密码"
      class="password login-field"
      placeholder="请输入密码"
      type="password"
      inputType="password"
      rule="^.{6,16}$"
      @onContentUpdate="(res) => (model.password = res)"
    />
    <LoginBtn @btnClick="onRegisterSubmit">登录</LoginBtn>
  </div>
</template>

<script>
import LoginTop from "@/components/login/LoginTop";
import LoginText from "@/components/login/LoginText";
import LoginBtn from "@/components/login/LoginBtn";
import { login } from "@/../http";

export default {
  components: {
    LoginTop,
    LoginText,
    LoginBtn,
  },
  data() {
    return {
      model: {
        username: undefined,
        password: undefined,
      },
    };
  },
  methods: {
    async onRegisterSubmit() {
      if (this.model.username && this.model.password) {
        const res = await login(this.model);
        if (res.data.status === 200) {
          this.$toast.success(res.data.message);
          localStorage.setItem("username", res.data.username);
          localStorage.setItem("token", res.data.token);
          setTimeout(() => {
            this.$router.push("/userinfo");
          }, 1000);
        } else {
          this.$toast.fail(res.data.message);
        }
      } else {
        if (this.$store.state.inputTips) {
          this.$toast.fail(this.$store.state.inputTips);
        } else {
          this.$toast.fail("输入错误");
        }
      }
    },
  },
};
</script>

<style>
.username {
  margin-top: 2.778vw;
}
</style>
